<template>
    <div class="home">
        <div class="djtIndex-top">
            <img src="../../assets/images/order-back.png" alt="" class="djtIndex-top-back">
            <div class="djtIndex-top-mean">
                <div class="djtIndex-top-mean-left">
                    <img src="../../assets/images/logo.png" alt="" class="djtIndex-top-mean-left-img">
                    <span class="djtIndex-top-mean-left-text">Donald John Trump</span>
                </div>
                <div class="djtIndex-top-mean-right">
                    <template v-if="!address">
                        <span class="djtIndex-top-mean-right-text" @click="showMask()">{{ short_address }}</span>
                    </template>
                    <template v-if="address">
                        <span class="djtIndex-top-mean-right-text">{{ short_address }}</span>
                    </template>

                    <img src="../../assets/images/index-lang.png" alt="" class="djtIndex-top-mean-right-img1" @click="showLang()">
                    <img src="../../assets/images/index-mean.png" alt="" class="djtIndex-top-mean-right-img2" @click="showMean">
                </div>
            </div>
            <div class="kuang-content">
                <div class="order-content" v-if="showOrder">
                    <div class="order-content-top">
                        <div>
                            <img src="../../assets/images/usdtnew.png" alt="" class="order-content-top-imgUsdt">
                            <img src="../../assets/images/djt.png" alt="" class="order-content-top-imgDji">
                        </div>
                        <div>DJT-USDT</div>
                    </div>
                    <div class="order-content-line"></div>
                    <div class="order-content-tab">
                        <div class="order-content-tab-left">
                            <div>{{ lang_list.order_desc1 }}：</div>
                            <div>{{ lang_list.order_desc2 }}：</div>
                            <div>{{ lang_list.order_desc3 }}：</div>
                            <div>{{ lang_list.order_desc4 }}：</div>
                        </div>
                        <div class="order-content-tab-right">
                            <div>{{ orderInfo.DJTQuantity }} DJT {{orderInfo.USDTQuantity}}USDT   </div>
                            <div>{{orderInfo.EndTimeNew}}</div>
                            <div>{{orderInfo.ExpirationTimeNew}}</div>
                            <div>
                                <span>{{orderInfo.profit}}DJT</span>
                                <div class="order-content-button-jie" @click="jieNow($event)">
                                    {{ lang_list.order_button1 }}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="kuang-content-submit order-content-button-back1" v-if="orderInfo.Out==1" @click="wateNow()">{{ lang_list.order_button2 }}</div>
                    <div class="kuang-content-submit" v-if="orderInfo.Out==2" @click="chuNow($event)">{{ lang_list.order_button3 }}</div>
                    <div class="kuang-content-submit order-content-button-back2" v-if="orderInfo.Out==0" @click="wateNow()">{{ lang_list.order_button4 }}</div>
                </div>
            </div>
        </div>

        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/images/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/images/close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-home.png" alt="">
                        <span>{{ lang_list.index_mean1 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toBuyDjt()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/duihuan.png" alt="">
                        <span>{{ lang_list.index_mean2 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toKuang()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-kuang.png" alt="">
                        <span>{{ lang_list.index_mean3 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-record.png" alt="">
                        <span>{{ lang_list.index_mean4 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-white.png" alt="">
                        <span>{{ lang_list.index_mean5 }}</span>
                    </div>
                </div>
                <div class="index_pop_bottom">
                    <div class="index_pop_bottom-left" @click="toTwitte()"><img src="../../assets/images/Twitte.png" alt=""></div>
                    <div class="index_pop_bottom-left" @click="todian()"><img src="../../assets/images/dianbao.png" alt=""></div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content" @click="checkNet()">
                <div class="index_mask-title">{{ lang_list.index_mask }}</div>
                <img src="../../assets/images/matemask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div :class="lang=='zh'?'index_lang-button-active':'index_lang-button'" @click="switchLang('zh')">中文</div>
            <div :class="lang=='en'?'index_lang-button-active':'index_lang-button'" @click="switchLang('en')">EN</div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract,djtContract,conContract;
    let baseUrl = "";
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                parents:'',
                meanShow: false,
                maskShow:false,
                orderInfo:{},
                rate:0.00125,
                jiedisable:false,
                chudisable:false,
                showOrder:false,
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.lang_list = this.en;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh;
                    break;
                case 'en':
                    this.lang_list = this.en;
                    break;
            }
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            wateNow(){
                this.$toast(this.lang_list.order_warn1);
            },
            async chuNow(event){
                let _this = this;
                if(_this.chudisable){
                    return false;
                }
                _this.chudisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice()
                    let res = await conContract.Wl({
                        gasPrice: gasPrice,
                        gasLimit:1000000
                    })
                    if(res){
                        _this.chudisable = false;
                        _this.$toast(_this.lang_list.order_warn2);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast(_this.lang_list.order_warn3);
                    _this.chudisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async jieNow(event){
                let _this = this;
                if(parseInt(this.orderInfo.ExpirationTime)<=0){
                    _this.$toast(_this.lang_list.order_warn4);
                    return false;
                }
                if(_this.jiedisable){
                    return false;
                }
                _this.jiedisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice()
                    let res = await conContract.Withdrawal({
                        gasPrice: gasPrice,
                        gasLimit:1000000
                    })
                    if(res){
                        _this.jiedisable = false;
                        _this.$toast(_this.lang_list.order_warn2);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast(_this.lang_list.order_warn3);
                    _this.jiedisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            todian(){
                window.open('https://t.me/DJT_Group','_self');
            },
            toTwitte(){
                window.open('https://twitter.com/DJTsystem','_self');
            },
            toBuyDjt(){
                window.open('https://pancakeswap.finance/swap?outputCurrency=0x41D8675E6C64939c3245908EEaf827A995Fc47Ff&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket','_self');
            },
            toContract(){
                window.open('https://bscscan.com/address/'+this.api.djtAddress,'_self');
            },
            toWhite(){
                window.open('/static/djt.pdf');
            },
            toIndex(){
                this.$router.push({
                    name: 'home',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toOrder(){
                this.$router.push({
                    name: 'order',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toKuang(){
                this.$router.push({
                    name: 'kuang',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type){
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list.index_warn);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        conContract = new ethers.Contract(_this.api.conAddress, conAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.getOrder(defaultAccount);
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getOrder(address){
                let _this = this;
                let startTime,dayTime;
                await conContract.Price().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.rate = parseFloat(ethers.utils.formatEther(res)*10000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract._startTime().then(res => {
                    startTime =  parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.oneDay().then(res => {
                    dayTime = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract._playerMap(address).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.orderInfo.address = ethers.utils.formatEther(res.addr);
                    _this.orderInfo.USDTQuantity = ethers.utils.formatEther(res.USDTQuantity);
                    _this.orderInfo.DJTQuantity  = ethers.utils.formatEther(res.DJTQuantity)*1000000000;
                    _this.orderInfo.profit = parseInt(ethers.utils.formatEther(res.profit)*1000000000*1000000000)/1000000000;
                    let EndTime = parseInt(ethers.utils.formatEther(res.EndTime)*1000000000000000000);
                    let ExpirationTime = parseInt(ethers.utils.formatEther(res.ExpirationTime)*1000000000000000000);
                    _this.orderInfo.Out = parseInt(ethers.utils.formatEther(res.Out)*1000000000000000000);
                    // _this.orderInfo.EndTime =  1648799483;
                    // _this.orderInfo.ExpirationTime =  1648799383;
                    _this.orderInfo.EndTimeNew='0000-00-00 00:00:00';
                    _this.orderInfo.ExpirationTimeNew='0000-00-00 00:00:00';
                    let timestamp =parseInt(Date.parse(new Date())/1000);
                    _this.orderInfo.EndTime  = parseInt(startTime)+ parseInt(EndTime)*parseInt(dayTime);
                    _this.orderInfo.ExpirationTime  = parseInt(startTime)+(parseInt(ExpirationTime)+parseInt(EndTime))*parseInt(dayTime);
                    if(EndTime>0&&parseInt(_this.orderInfo.EndTime)>0){
                        _this.orderInfo.EndTimeNew = _this.formatDate(parseInt(_this.orderInfo.EndTime))
                    }
                    if(ExpirationTime>0&&parseInt(_this.orderInfo.ExpirationTime)>0){
                        _this.orderInfo.ExpirationTimeNew = _this.formatDate(parseInt(_this.orderInfo.ExpirationTime))
                    }
                    if(_this.orderInfo.ExpirationTime<=timestamp){
                        _this.orderInfo.Out =2;
                    }
                    _this.showOrder=true;
                    console.log( _this.orderInfo)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            formatDate(time) {
                var date = new Date(time*1000);
                var YY = date.getFullYear() + '-';
                var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
                var hh = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
                var mm = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
                var ss = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
                return YY + MM + DD +" "+hh + mm + ss;
            }
        }
    }
</script>


